<!--
 * @Author: zgl
 * @Date: 2020-12-17 20:59:47
 * @LastEditTime: 2021-01-12 09:07:25
 * @LastEditors: Please set LastEditors
 * @Description: In User Settings Edit
 * @FilePath: \工作项目协同开发\operation_system\src\components\shopManagement\pagemaintenance\NegativeComment.vue
-->
<template>
  <div id="ipi-board">
    <div class="left">
      <div class="title">
        <h2 style="text-align:center">页面维护-首页无差评</h2>
      </div>
      <div class="left-content">
        <h3>请选择店铺</h3>
        <!-- 菜单 -->
        <el-menu
          class="el-menu-vertical-demo"
          unique-opened
          default-active="0-0-0"
        >
          <el-submenu
            :index="index + ''"
            v-for="(item, index) in menuPlatFormList"
            :key="index"
          >
            <template slot="title">
              <span
                style="display:inline-block;width:100%"
                @click="onceClick(index)"
                >{{ item }}</span
              >
            </template>
            <el-submenu
              :index="index + '-' + index2"
              v-for="(item2, index2) in showCountryList"
              :key="index2"
            >
              <template slot="title">
                <span
                  style="display:inline-block;width:100%"
                  @click="secondClick(index2)"
                  >{{ item2 }}</span
                >
              </template>
              <el-menu-item
                :index="index + '-' + index2 + '-' + index3"
                v-for="(item3, index3) in lastSiteList"
                :key="index3"
                @click="thirdClick(item, item2, item3)"
              >
                <span>{{ item3 }}</span>
              </el-menu-item>
            </el-submenu>
          </el-submenu>
        </el-menu>
      </div>
    </div>
    <div class="right">
      <span>差评维护</span>
      <div class="button">
        <span>按sku筛选：</span>
        <el-select
          v-model="checkedSku"
          clearable
          placeholder="请选择"
          size="mini"
          @change="skuChange"
        >
          <el-option
            v-for="(item, index) in skuList"
            :key="index"
            :label="item"
            :value="item"
          ></el-option>
        </el-select>
        <el-button
          type="primary"
          size="mini"
          @click="handleAll"
          style="margin-left:12px"
          >全部</el-button
        >
        <el-button type="primary" size="mini" @click="handleBefore"
          >未处理</el-button
        >
        <el-button type="primary" size="mini" @click="handling"
          >处理中</el-button
        >
        <el-button type="primary" size="mini" @click="handleAfter"
          >已处理</el-button
        >
        <el-button type="primary" size="mini" @click="exportExcel"
          >导出Excel</el-button
        >
      </div>
      <div class="table">
        <el-table
          ref="ncTable"
          :data="badCommentList"
          border
          style="width: 100%"
          max-height="700"
          @selection-change="handleSelectionChange"
          v-loading="loading"
          element-loading-text="拼命加载中"
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(255, 255, 255, 0.8)"
        >
          <el-table-column type="selection"></el-table-column>
          <el-table-column
            type="index"
            :index="indexMethod"
            label="序号"
          ></el-table-column>
          <el-table-column prop="0" label="渠道"></el-table-column>
          <el-table-column prop="1" label="站点" width="70"></el-table-column>
          <el-table-column prop="2" label="国家" width="70"></el-table-column>
          <af-table-column prop="3" label="sku"></af-table-column>
          <el-table-column prop="4" label="ASIN"></el-table-column>
          <el-table-column prop="5" label="抓取时间"></el-table-column>
          <el-table-column prop="6" label="客户名"></el-table-column>
          <el-table-column prop="7" label="星级" width="50"></el-table-column>
          <el-table-column prop="8" label="评论发布时间"></el-table-column>
          <el-table-column prop="9" label="前台链接">
            <template slot-scope="scope">
              <el-link :href="scope.row[9]" target="_blank" type="success"
                >点我跳转</el-link
              >
            </template>
          </el-table-column>
          <el-table-column prop="10" label="客户评论链接">
            <template slot-scope="scope">
              <el-link :href="scope.row[10]" target="_blank" type="success"
                >点我跳转</el-link
              >
            </template>
          </el-table-column>
          <el-table-column prop="11" label="处理情况">
            <template slot-scope="scope">
              <span v-if="scope.row[11] !== '处理中'">{{ scope.row[11] }}</span>
              <el-link type="danger" v-else @click="goHandle(scope.row)">{{
                scope.row[11]
              }}</el-link>
            </template>
          </el-table-column>
          <af-table-column prop="12" label="处理时间"></af-table-column>
          <el-table-column prop="13" label="处理人"></el-table-column>
        </el-table>
        <el-pagination
          style="text-align:center;margin-top: 10px;"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          layout="total,prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
      </div>
    </div>

    <!-- 上传差评处理对话框 -->
    <!-- <el-dialog title="上传差评处理截图" :visible.sync="handleDialogVisble" width="30%" center>
      <div>
        <span>差评处理前：</span>
        <el-upload
          class="upload-demo"
          action="https://jsonplaceholder.typicode.com/posts/"
          list-type="picture"
          :before-upload="beforeUpload"
          :http-request="uploadFile"
          :on-remove="handleRemove"
        >
          <el-button size="small" type="primary">点击上传</el-button>
          <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
        </el-upload>
      </div>
      <div>
        <span>差评消除后：</span>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="handleDialogVisble = false">提 交</el-button>
      </span>
    </el-dialog>-->
  </div>
</template>
<script>
export default {
  name: "",
  props: {},
  components: {},
  data() {
    return {
      // 左侧导航栏菜单
      menuPlatFormList: [],
      menuCountryList: [],
      menuSiteList: [],
      showCountryList: [],
      showSiteList: [],
      lastSiteList: [],
      // 一级菜单和二级菜单是否是第一次点击
      platformOnce: [],

      // 品牌备案文件列表
      badCommentList: [],
      // 选中的菜单项
      checkedPlatform: "",
      checkedCountry: "",
      checkedSite: "",
      checkedStatus: "",

      // sku下拉框
      skuList: [],
      checkedSku: "",

      // 表格多选框
      selectNum: "",
      selection: [],

      // 上传差评处理对话框
      handleDialogVisble: false,
      file: "",
      fileData: [],
      // 分页相关
      currentPage: 1,
      total: 0,
      pageSize: 20,

      // 导出Excel表
      // 头部
      headData: [
        { title: "渠道", key: "0" },
        { title: "站点", key: "1" },
        { title: "国家", key: "2" },
        { title: "sku", key: "3" },
        { title: "ASIN", key: "4" },
        { title: "抓取时间", key: "5" },
        { title: "客户名", key: "6" },
        { title: "评价星级", key: "7" },
        { title: "评论发布时间", key: "8" },
        { title: "前台链接", key: "9" },
        { title: "客户评论链接", key: "10" },
        { title: "处理情况", key: "11" },
        { title: "处理时间", key: "12" },
        { title: "处理人", key: "13" }
      ],
      loading: true
    };
  },
  computed: {},
  watch: {},
  methods: {
    // 获取菜单栏数据
    getMenuList() {
      this.$api.getMenuList().then(res => {
        // console.log(res);
        this.menuPlatFormList = res.data.data1;
        this.menuCountryList = res.data.data2;
        this.menuSiteList = res.data.data3;

        // 页面刷新 先默认展示京汇的 同时在导航那里添加default-active="0-0-1"
        this.showCountryList = this.menuCountryList[0];
        this.showSiteList = this.menuSiteList[0];
        this.lastSiteList = this.showSiteList[0];
        this.checkedPlatform = this.menuPlatFormList[0];
        this.checkedCountry = this.showCountryList[0];
        this.checkedSite = this.lastSiteList[0];
        this.thirdClick(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite
        );
      });
    },
    /**
     * @description: 获取sku列表
     * @param {*}
     * @return {*}
     */
    // getSkuList() {
    //   let page = 1;
    //   this.$api.queryBadComment("", "", "", page).then(res => {
    //     console.log(res);
    //     this.skuList = res.data.all_sku_list;
    //   });
    // },
    /**
     * @description: sku被选择
     * @param {*}
     * @return {*}
     */
    skuChange(sku) {
      console.log(sku);

      let page = 1;
      // 选择sku后，先展示全部
      this.checkedStatus = "";
      this.loading = true;
      this.$api
        .queryBadComment(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite,
          page,
          this.checkedSku,
          this.checkedStatus
        )
        .then(res => {
          console.log(res);
          this.badCommentList = this.$utils.deepClone(res.data.data);
          this.badCommentList = this.badCommetListSort(this.badCommentList);
          this.loading = false;
          this.currentPage = 1;
          this.total = res.data.all_number;
        });
    },
    onceClick(index) {
      // if(this.platformOnce[index] == "") {
      this.showCountryList = [];
      this.showSiteList = [];
      this.showCountryList = this.menuCountryList[index];
      this.showSiteList = this.menuSiteList[index];
      this.platformOnce[index] = 1;
      // console.log(this.platformOnce);
      // }
    },
    secondClick(index) {
      // console.log(index);
      this.lastSiteList = [];
      this.lastSiteList = this.showSiteList[index];
    },
    thirdClick(platform, country, site) {
      this.checkedPlatform = platform;
      this.checkedCountry = country;
      this.checkedSite = site;
      // 点击左边导航需要将状态和sku置空
      this.checkedStatus = "";
      this.checkedSku = "";
      let page = 1;
      this.loading = true;
      this.$api.queryBadComment(platform, country, site, page).then(res => {
        console.log(res);
        this.skuList = res.data.all_sku_list;
        this.badCommentList = this.$utils.deepClone(res.data.data);
        this.badCommentList = this.badCommetListSort(this.badCommentList);
        this.$nextTick(() => {
          this.$refs["ncTable"].bodyWrapper.scrollTop = 0;
        });
        this.loading = false;
        this.currentPage = 1;
        this.total = res.data.all_number;
      });
    },
    // 封装方法，将badCommetList未处理放前面
    badCommetListSort(arr) {
      let newArr = [];
      // 将未处理的先push进新的数组，同时，旧的数组要删除未处理的一项，最后旧的数组就只剩已处理的
      for (let i = 0; i < arr.length; i++) {
        if (arr[i][11] == "未处理") {
          newArr.push(arr[i]);
          arr.splice(i, 1);
          i--;
        }
      }
      // 将只剩已处理的旧数组push进新数组
      newArr.push(...arr);
      return newArr;
    },
    // 下拉框联合
    selectChange(val) {
      // console.log(val);
    },
    // 未处理
    handleBefore() {
      console.log("未处理");

      this.checkedStatus = "未处理";
      let page = 1;
      // if (
      //   this.checkedPlatform.length > 0 &&
      //   this.checkedCountry.length > 0 &&
      //   this.checkedSite.length > 0
      // ) {
      this.loading = true;
      this.$api
        .queryBadComment(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite,
          page,
          this.checkedSku,
          this.checkedStatus
        )
        .then(res => {
          console.log(res);
          this.badCommentList = res.data.data;
          this.$nextTick(() => {
            this.$refs["ncTable"].bodyWrapper.scrollTop = 0;
          });
          this.loading = false;
          this.currentPage = 1;
          this.total = res.data.all_number;
        });
      // } else {
      //   return;
      // }
    },
    // 处理中
    handling() {
      console.log("处理中");
      this.checkedStatus = "处理中";
      let page = 1;
      // if (
      //   this.checkedPlatform.length > 0 &&
      //   this.checkedCountry.length > 0 &&
      //   this.checkedSite.length > 0
      // ) {
      this.loading = true;
      this.$api
        .queryBadComment(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite,
          page,
          this.checkedSku,
          this.checkedStatus
        )
        .then(res => {
          console.log(res);
          this.badCommentList = res.data.data;
          this.$nextTick(() => {
            this.$refs["ncTable"].bodyWrapper.scrollTop = 0;
          });
          this.loading = false;
          this.currentPage = 1;
          this.total = res.data.all_number;
        });
      // } else {
      //   return;
      // }
    },
    // 已处理
    handleAfter() {
      console.log("已处理");

      this.checkedStatus = "已处理";
      let page = 1;
      // if (
      //   this.checkedPlatform.length > 0 &&
      //   this.checkedCountry.length > 0 &&
      //   this.checkedSite.length > 0
      // ) {
      this.loading = true;
      this.$api
        .queryBadComment(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite,
          page,
          this.checkedSku,
          this.checkedStatus
        )
        .then(res => {
          console.log(res);
          this.badCommentList = res.data.data;
          this.$nextTick(() => {
            this.$refs["ncTable"].bodyWrapper.scrollTop = 0;
          });
          this.loading = false;
          this.currentPage = 1;
          this.total = res.data.all_number;
        });
      // } else {
      //   return;
      // }
    },
    handleAll() {
      console.log("全部");

      let page = 1;
      this.checkedStatus = "";
      // if (
      //   this.checkedPlatform.length > 0 &&
      //   this.checkedCountry.length > 0 &&
      //   this.checkedSite.length > 0
      // ) {
      this.loading = true;
      this.$api
        .queryBadComment(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite,
          page,
          this.checkedSku
        )
        .then(res => {
          console.log(res);
          this.badCommentList = this.$utils.deepClone(res.data.data);

          this.badCommentList = this.badCommetListSort(this.badCommentList);
          this.$nextTick(() => {
            this.$refs["ncTable"].bodyWrapper.scrollTop = 0;
          });
          this.loading = false;
          this.currentPage = 1;
          this.total = res.data.all_number;
        });
      // } else {
      //   return;
      // }
    },
    // 将处理中变成已处理
    goHandle(row) {
      // console.log(id);
      // let idList = [];
      // idList.push(id);
      let info = [];
      info.push(row[2], row[1], row[3], row[7]);
      // this.handleDialogVisble = true;
      this.$confirm("确定差评已消除了吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$api.changeStatus(idList, "已处理").then(res => {
            console.log(res);
            this.$message.success("处理成功");
            let page = 1;
            this.loading = true;
            this.$api
              .queryBadComment(
                this.checkedPlatform,
                this.checkedCountry,
                this.checkedSite,
                page,
                this.checkedSku,
                this.changeStatus
              )
              .then(res => {
                this.badCommentList = this.$utils.deepClone(res.data.data);
                this.badCommentList = this.badCommetListSort(
                  this.badCommentList
                );
                this.loading = false;
                this.currentPage = 1;
                this.total = res.data.all_number;
              });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消回复"
          });
        });
    },
    beforeUpload(file) {
      //文件上传前发生
      console.log(file);
      this.fileData.push(file);
      this.file = file;
    },
    uploadFile(file) {
      // console.log(file.file);
      // this.fileData.push(file.file);
    },
    handleRemove(file, fileList) {
      // console.log(fileList);
      // console.log(file);
    },
    indexMethod(val) {
      return val + 1 + (this.currentPage - 1) * 50;
    },
    // 分页
    handleCurrentChange(val) {
      console.log(val);
      this.loading = true;
      this.$api
        .queryBadComment(
          this.checkedPlatform,
          this.checkedCountry,
          this.checkedSite,
          val,
          this.checkedSku,
          this.checkedStatus
        )
        .then(res => {
          console.log(res);
          this.badCommentList = res.data.data;
          this.$nextTick(() => {
            this.$refs["ncTable"].bodyWrapper.scrollTop = 0;
          });
          this.loading = false;
          this.total = res.data.all_number;
        });
    },
    /**
     * @description: 导出Excel
     * @param {*}
     * @return {*}
     */
    exportExcel() {
      // 获取id列表
      // let idList = [];
      // for (let item of this.selection) {
      //   idList.push(item[0]);
      // }
      // 获取站点国家sku评论时间
      let info = [];
      for (let item of this.selection) {
        console.log(this.selection);
        let arr = [];
        arr.push(item[2], item[1], item[3], item[8]);
        info.push(arr);
      }
      // 获取客户评论链接
      // let commentList = [];
      // for (let item of this.selection) {
      //   commentList.push(item[9]);
      // }
      console.log(info);

      // 如果没有选择要提示一下用户 同时不让其执行请求接口这一步 要return
      if (this.selection.length === 0) {
        return this.$confirm("你并没有勾选数据哟, 还要导出Excel吗?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$utils.export2Excel(this.headData, this.selection);
          })
          .catch(() => {});
      }

      this.$api.changeStatus(info, "处理中").then(res => {
        console.log(res);
        let page = 1;
        this.loading = true;
        this.$api
          .queryBadComment(
            this.checkedPlatform,
            this.checkedCountry,
            this.checkedSite,
            page,
            this.checkedSku,
            this.changeStatus
          )
          .then(res => {
            this.badCommentList = res.data.data;
            this.badCommentList = this.badCommetListSort(this.badCommentList);
            this.loading = false;
            this.currentPage = 1;
            this.total = res.data.all_number;
          });
      });
      this.$utils.export2Excel(this.headData, this.selection);
    },
    /**
     * @description: 多选框的选中事件
     * @param {*} selection
     * @return {*}
     */
    handleSelectionChange(selection) {
      this.selectNum = selection.length;
      this.selection = selection;
      console.log(this.selection, this.selectNum);
    }
  },
  created() {
    this.getMenuList();
    // this.thirdClick("", "", "");
    // this.getSkuList();
  },
  mounted() {}
};
</script>
<style scoped>
#ipi-board {
  width: 100vw;
  height: 92vh;
  position: relative;
}
.left {
  position: absolute;
  width: 16vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  border-right: 1px solid black;
  top: 0;
  left: 0;
  overflow: auto;
}
.right {
  position: absolute;
  top: 0;
  right: 0;
  width: 80vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  left: 18vw;
  padding: 24px;
  overflow: auto;
}
.title {
  margin-top: 2vh;
}
.left-content h3 {
  margin-top: 2vh;
  text-align: center;
}
.button {
  margin-bottom: 2vh;
}
</style>



